import React from 'react';
import "../Cards/Card.css";

const Card = (props) => {
  return (
    <>
        <div className='col-lg-4 col-md-4' data-aos="fade-down" data-aos-delay="500">
                <div class="card my-2" id='upper'>
                    <img class="card-img-top" src={props.images} alt="Card image cap" />
                    <div class="card-body country" >
                        <h5 class="card-title"  >{props.tittle}</h5>
                        <p class="card-text">{props.text}</p>
                        <p>{props.date}</p>
                    </div>
                </div>
            </div>
    </>
  )
}

export default Card