import React from 'react';
import Card from "../Frontend/Frontend";
import Frontdata from './Frontdata';
import "../Frontend/Frontend.css"


const Frontends = () => {
  return (
    <>
    <div className="front-heading">
            <h1>Front-End Development</h1>
    </div>
        
        <div className='container py-5' id='cardtxt'  >
                    <div className='row pt'>
                        {Frontdata.map((val) => {
                            return (
                                <Card
                                    images={val.images}
                                    tittle={val.tittle}
                                    text={val.text}
                                    date={val.date}
                                />
                            )
                        })}
                    </div>
                </div>
    </>
  )
}

export default Frontends;