import React from 'react';
import "../FAQs/Faqs.css";
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

const Faqs = () => {
    const [expanded, setExpanded] = React.useState(false);

    const handleChange = (panel) => (event, isExpanded) => {
      setExpanded(isExpanded ? panel : false);
    }
  return (
    
    <>
    <div className="container-fluid">
        <div className="container">
            <div className="row">
                <div className="col-lg-6 questions" data-aos="flip-right" data-aos-delay="500">
                        <h1>Frequently Asked Questions</h1>
                        <p>The Rocket Monsters team is always working on different aspects of the Rocket Monsters Universe. If your question isn't answered here please visit us on Discord or Twitter!</p>
                </div>
                <div className="col-lg-6 " >
                <div data-aos="flip-down" data-aos-delay="500">
      <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')} className="accordian my-3" >
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel4bh-content"
          id="panel4bh-header"
        >
          <Typography sx={{ width: '100%', flexShrink: 0 }} className="accordiantxt">What Blockchain is Rocket Monster building on?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
          <p className="accordiantxtp">Rocket Monsters is a multi-chain game with NFT interoperability and full NFT integration into Unreal Engine 5.</p>
          
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')}  className="accordian my-3">
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel4bh-content"
          id="panel4bh-header"
        >
          <Typography sx={{ width: '100%', flexShrink: 0}} className="accordiantxt">What Blockchain is Rocket Monster building on?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            <p className="accordiantxtp"> Rocket Monsters is a multi-chain game with NFT interoperability and full NFT integration into Unreal Engine 5.</p>
        
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')}  className="accordian my-3">
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel4bh-content"
          id="panel4bh-header"
        >
          <Typography sx={{ width: '100%', flexShrink: 0 }} className="accordiantxt">What Blockchain is Rocket Monster building on?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
          <p className="accordiantxtp">Rocket Monsters is a multi-chain game with NFT interoperability and full NFT integration into Unreal Engine 5.</p>
          
          </Typography>
        </AccordionDetails>
      </Accordion>
                </div>
                </div>
            </div>
        </div>
        </div>
    </>
  )
}

export default Faqs