import React from 'react';
import "../About/About.css"

const About = () => {
  return (
    <>
   
    <div className="container-fluid" id='about-'>
        <div className="container about"  >
            <div className="col-lg-10 mx-auto my-5" data-aos="fade-right" data-aos-delay="800">
                <h1>About Us</h1>
                <p>Rocket Monsters is a Play and Own Blockchain game built on Unreal Engine 5. The Rocket Monsters Universe is filled with a variety of characters, weapons, and vehicles which are all playable NFTs. Players will engage in PVP, and PVE across multiple areas with endless possibilities. With robust Tokenomics woven into the game, players will be introduced to vibrant and meaningful gameplay! We are building the next evolution in Blockchain games. We are building a player centered Universe.</p>
            </div>
        </div>
        </div>
    </>
  )
}

export default About