import React from 'react';
import "../RoadMap/Roadmap.css"

const Roadmap = () => {
  return (
    <>
        <div className="container roadmap my-5" id='map'>
            <h1 >RoadMap</h1>
            <div className="mapline ">
            </div>
            <div className="col-lg-5 col-md-5 left my-2">
            <div className="mapbox " data-aos="flip-left" data-aos-delay="1500" >

            <h2>May 2022</h2>
            <p>Rocket Monsters Series 1 and Series 2 NFT drop. Vehicle introduction, website, whitepaper, and Tokenomics</p>
            </div>
            </div>
            <div className="col-lg-5 col-md-5 right">
            <div className="mapbox2" data-aos="flip-right" data-aos-delay="1500">
                <h2>June 2022</h2>
                <p>Land website for NFT drop created, XP Network bridge integration, and Multiplayer public pre-beta dropped.</p>
            </div>
            </div>
            <div className="col-lg-5 col-md-5 left my-2" >
            <div className="mapbox2" data-aos="flip-left" data-aos-delay="1500">
                <h2>July 2022</h2>
                <p>The Dome PVP area build and public pre-beta drop. Spaceship builds began for future NFT drops. Nightly Multiplayer hosted online for testing.</p>
            </div>
            </div>
            <div className="col-lg-5 col-md-5 right" >
            <div className="mapbox2" data-aos="flip-left" data-aos-delay="1500">
                <h2>August 2022</h2>
                <p>Continue PVP areas including PVP racing, finalize XP Network bridge, and full NFT and wallet integration into Unreal Engine for public testing.</p>
            </div>
            </div>

            
            
        </div>
    </>
  )
}

export default Roadmap