import React from 'react';
import Carddata from './Cdata';
import Card from './Card';

const Cards = () => {
  return (
    <>
        <div className='container py-5' id='cardtxt' >
                    <div className='row'>
                        {Carddata.map((val) => {
                            return (
                                <Card
                                    images={val.images}
                                    tittle={val.tittle}
                                    text={val.text}
                                    date={val.date}
                                />
                            )
                        })}
                    </div>
                </div>
    </>
  )
}

export default Cards