import React from 'react';
import "../Frontend/Frontend.css"

const Frontend = (props) => {
  return (
    <>
        <div className='col-lg-3 col-md-6'>
                <div class="card my-2" id='frontcard' data-aos="fade-down" data-aos-delay="500">
                    <img class="card-img-top" src={props.images} alt="Card image cap" />
                    <div class="card-body country p-0" >
                        <h5 class="card-title"  >{props.tittle}</h5>
                        <p class="card-text para">{props.text}</p>
                        <p className='date'>{props.date}</p>
                    </div>
                </div>
          </div>
    </>
  )
}

export default Frontend;